<template>
  <div>
    <div id="glyphs-container">
      <GlyphDisplayer
        v-for="id in glyphCount"
        v-bind:key="id"
        :glyph="glyphs[id - 1]"
        :active="isActive[id - 1]"
        :groupStyles="styleParams[id - 1]"
        :symbolAnimStyles="{
          'animation-duration': `${this.animParams.durations[id - 1]}s`,
          'animation-delay': `${this.animParams.delays[id - 1]}s`,
        }"
        @click="setActiveId(id - 1)"
      />
    </div>
  </div>
</template>

<script>
import GlyphDisplayer from "../components/GlyphDisplayer.vue";
import glyphsData from "../assets/glyphs.json";

export default {
  data() {
    return {
      activeId: 0,
      glyphs: glyphsData.glyphs,
    };
  },
  computed: {
    isActive() {
      let active = [];
      for (let i = 0; i < this.glyphCount; i++) {
        active.push(i === this.activeId);
      }
      return active;
    },
    animParams() {
      let durations = [];
      let delays = [];
      for (let i = 0; i < this.glyphCount; i++) {
        durations.push(4 + 0.4 * Math.random());
        delays.push(4 * Math.random());
      }
      return {
        durations: durations,
        delays: delays,
      };
    },
    styleParams() {
      let ACTIVE_W = 500;
      let INACTIVE_W = 150;
      let OFFSET_W = 50;
      let OFFSET_H = 50;
      let ACTIVE_H = 400;

      let count = this.glyphCount;
      let half = (count + 1) / 2;
      let params = {};

      let rearrangedIds = [];
      let startId = (half + this.activeId) % count;
      for (let i = startId; i < count; i++) {
        rearrangedIds.push(i);
      }
      for (let i = 0; i < startId; i++) {
        rearrangedIds.push(i);
      }
      for (let i in rearrangedIds) {
        let componentId = rearrangedIds[i];
        let diff = i - half + 1;
        let left_most = OFFSET_W - ACTIVE_W / 2 - INACTIVE_W * (half - 1);
        let left_diff = left_most + i * INACTIVE_W;
        if (diff > 0) {
          left_diff += ACTIVE_W - INACTIVE_W;
        }
        let top_diff = Math.abs(diff) * 10 - ACTIVE_H / 2 + OFFSET_H;

        let param = {
          left: `${left_diff}px`,
          top: `${top_diff}px`,
          transform: `scale(${1 - Math.abs(diff) * 0.1})`,
        };
        params[componentId] = param;
      }
      return params;
    },
    glyphCount() {
      return this.glyphs.length;
    },
  },
  components: {
    GlyphDisplayer,
  },
  methods: {
    setActiveId(id) {
      this.activeId = id;
    },
  },
};
</script>

<style scoped>
#glyphs-container {
  position: relative;
  width: 100px;
  height: 100px;
  display: block;
}
</style>
