<template>
  <div class="glyph-displayer" :style="groupStyles" :class="{ active: active }">
    <div class="symbol-column">
      <div
        v-if="active"
        :style="{ ...symbolAnimStyles, ...alteringStyle }"
        class="glyph-symbol"
      >
        {{ currentSymbol }}
      </div>
      <div
        v-else
        :style="{ ...symbolAnimStyles, ...alteringStyle }"
        class="glyph-inactive-symbol"
        :class="{ altering: altering }"
      >
        {{ currentSymbol }}
      </div>

      <div v-if="active" class="glyph-title">{{ glyph.title }}</div>
      <div v-else class="glyph-inactive-title">{{ glyph.title }}</div>
    </div>
    <div
      v-if="active"
      :class="{ active: active }"
      class="glyph-desc"
      v-html="glyph.description"
    ></div>
  </div>
</template>

<script>
export default {
  props: {
    glyph: Object,
    active: Boolean,
    groupStyles: Object,
    symbolAnimStyles: Object,
  },
  data() {
    return {
      timer: 0,
      last_timestamp: 0,
    };
  },
  computed: {
    currentSymbol() {
      if (this.glyph.alter) {
        return this.timer % (this.ALTER_TIME * 2) < this.ALTER_TIME
          ? this.glyph.symbol
          : this.glyph.alter;
      }
      return this.glyph.symbol;
    },
    alteringStyle() {
      if (!this.glyph.alter) {
        return {};
      }
      let diff = this.timer % this.ALTER_TIME;
      diff = Math.min(diff, this.ALTER_TIME - diff) / this.ALTER_TIME;
      let opacity = diff < 0.2 ? diff / 0.2 : 1;
      return {
        opacity: opacity,
      };
    },
  },
  mounted() {
    this.ALTER_TIME = 3000;
    requestAnimationFrame(this.animate);
  },
  methods: {
    animate(timestamp) {
      if (this.last_timestamp === 0) {
        this.timer = 0;
        this.last_timestamp = timestamp;
      } else {
        this.timer += timestamp - this.last_timestamp;
        this.last_timestamp = timestamp;
      }
      requestAnimationFrame(this.animate);
    },
  },
};
</script>

<style scoped>
.glyph-displayer {
  position: absolute;
  width: 150px;
  height: 400px;
  transition-duration: 0.5s;
  /* border: 1px inset #fff; */
  color: #ccc;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.glyph-displayer.active {
  width: 500px;
}

.glyph-desc {
  font-size: 14px;
  letter-spacing: -0.5px;
  transition-duration: 1s;
  animation-name: description-opacity;
  animation-duration: 1s;
  padding-left: 13px;
  color: #aaa;
}

.symbol-column {
  display: flex;
  position: relative;
  min-height: 400px;
  min-width: 150px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.symbol-column::after {
  display: flex;
  content: "";
  position: absolute;
  top: 20px;
  left: 0px;
  min-height: 400px;
  min-width: 150px;
  background-image: url("../assets/stele_bg.svg");
  background-repeat: no-repeat;
  filter: drop-shadow(3px 15px #ccc5);
  z-index: -1;
}

.glyph-displayer:not(.active) > .symbol-column {
  cursor: pointer;
}

.glyph-inactive-symbol,
.glyph-symbol {
  font-family: Garamond, "Times New Roman", sans-serif;
  font-size: 90px;
  animation: symbol-float ease-in-out;
  animation-iteration-count: infinite;
  text-shadow: 0px 0px 30px #fff;
}

.glyph-inactive-title,
.glyph-title {
  padding-top: 10px;
  font-family: "SimSun", "Microsoft YaHei", "PingFang SC", "san-serif";
  font-size: 21px;
  color: #999;
  text-decoration: overline;
  /* text-decoration: double; */
  text-shadow: 2px 2px 4px #0004;
}

@keyframes symbol-float {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, -12px);
  }
  100% {
    transform: translate(0, 0);
  }
}

@keyframes description-opacity {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
</style>
